.square {
    width: 65px;
    height: 65px;
    background-color: transparent;
    font-size: 65px;
    color: transparent;
    border: none;
    cursor: pointer;
    text-shadow: 0px 5px 10px 2px rgba(34, 60, 80, 1);
    text-align: center;
    // margin: 0 0 10px 10px;
    user-select: none;
}

@media (max-width: 576px) {
    .square {
        padding-top: 5px;
        width: 37px;
        height: 37px;
        background-color: transparent;
        color: transparent;
        border: none;
        cursor: pointer;
        text-shadow: 0px 5px 10px 2px rgba(34, 60, 80, 1);
        text-align: center;
        // margin: 0 0 10px 10px;
        user-select: none;
    }
}
